.maps {
	padding: 5%;

	&__text {
		text-align: center;
		font-size: 15px;
		font-family: @font-family;
		color: @color;
	}

	&__img {
		text-align: center;
	}

		h2 {
			text-align: center;
		}

	&__transport {
		display: flex;
		justify-content: space-around;;
		margin-left: 200px;
		margin-right: 200px;
		}

	&__bus {
		background-image: url(../img/bus.png);
		background-repeat: no-repeat;
		background-position: center;
		background-size: 80px;
		padding-top: 40px;
	}

	&__train {
		background-image: url(../img/electro.png);
		background-repeat: no-repeat;
		background-position: center;
		background-size: 80px;
		padding-top: 40px;
		padding-top: 40px;
	}

}
