body {
  min-width: 320px;
  margin: 0;
  font-family: 'Fira Sans', sans-serif;
  color: #000;
  font-weight: 700;
}
.promo {
  display: block;
}
.promo__title {
  background: white;
  width: 35%;
}
.promo__text {
  background: white;
  width: 35%;
}
.promo__main {
  margin-left: 150px;
  position: absolute;
  top: 200px;
  width: 100%;
  font-family: 'Fira Sans', sans-serif;
  font-weight: normal;
}
.btn {
  display: inline-block;
  padding: 5px;
  border-radius: 60px;
  font-family: 'Fira Sans', sans-serif;
  text-decoration: none;
  touch-action: none;
}
.comment {
  margin-top: 50px;
  background-size: cover;
  padding-top: 5px;
  background-image: url(../img/bike.png);
}
.comment__recalls {
  display: flex;
  justify-content: center;
  padding: 50px;
  padding: 0px;
}
.comment__label {
  text-align: center;
  font-size: 30px;
  font-family: 'Fira Sans', sans-serif;
  font-weight: lighter;
  letter-spacing: 5px;
  color: white;
}
.comment__item {
  text-align: center;
  background: white;
  padding: 15px;
  margin-left: 20px;
  margin-right: 20px;
  width: 400px;
}
.comment__item-text {
  font-family: 'Fira Sans', sans-serif;
  font-weight: lighter;
  color: #B5B5B5;
}
.comment__all {
  text-align: center;
  padding: 15px;
}
.comment__img {
  border: white solid 4px;
  margin-top: -50px;
}
footer {
  display: flex;
  justify-content: center;
  border: 1px solid;
}
footer__addres {
  display: flex;
  justify-content: center;
}
footer__logo {
  text-align: center;
}
footer__social {
  float: center;
}
footer__info {
  float: right;
}
.main-nav {
  display: inline-block;
  margin: 30px;
  font-weight: normal;
  color: #808080;
  line-height: normal;
}
.main-nav__list {
  padding: 0px;
  display: inline;
  word-spacing: 15px;
  font-family: 'Fira Sans', sans-serif;
}
.main-nav__link__active {
  color: #ff7435;
}
.main-nav__item {
  display: inline-block;
  text-decoration: none;
}
.maps {
  padding: 5%;
}
.maps__text {
  text-align: center;
  font-size: 15px;
  font-family: 'Fira Sans', sans-serif;
  color: #808080;
}
.maps__img {
  text-align: center;
}
.maps h2 {
  text-align: center;
}
.maps__transport {
  display: flex;
  justify-content: space-around;
  margin-left: 200px;
  margin-right: 200px;
}
.maps__bus {
  background-image: url(../img/bus.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 80px;
  padding-top: 40px;
}
.maps__train {
  background-image: url(../img/electro.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 80px;
  padding-top: 40px;
}
.page-header {
  display: flex;
  justify-content: center;
  background-color: #fafafa;
  padding: 10px;
}
.page-header__logo {
  margin: 6px;
}
.page-header__contacts {
  font-family: 'Fira Sans', sans-serif;
  padding: 20px;
  font-weight: lighter;
}
.preview {
  text-align: center;
  background-image: url(../img/fon.png);
  background-position: 50% 50%;
  background-repeat: no-repeat;
}
.preview__tab {
  text-align: center;
  display: inline-block;
  text-decoration: none;
  font-family: 'Fira Sans', sans-serif;
  margin: 10px;
  color: #808080;
}
.preview a {
  color: #ff7435;
}
.preview__main {
  display: block;
  text-align: center;
  font-family: 'Fira Sans', sans-serif;
  font-weight: lighter;
  color: #464544;
}
.preview__norm {
  font-size: 15px;
  text-align: center;
  line-height: 1.5em;
}
.preview__staf {
  list-style: none;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-family: 'Fira Sans', sans-serif;
}
.preview__staf li {
  width: 90px;
  margin-bottom: 20px;
  text-align: center;
  font-size: 14px;
}
.review {
  position: relative;
  text-align: center;
  display: block;
}
.stock {
  text-align: center;
}
.stock__label {
  font-family: 'Fira Sans', sans-serif;
  font-weight: lighter;
  color: #808080;
  border: solid 1px;
}
.stock__item {
  background-image: url(../img/photo.png);
  background-repeat: no-repeat;
  margin-left: 130px;
  width: 1150px;
  height: 330px;
}
.stock__info {
  text-align: left;
  background: white;
  width: 35%;
  opacity: 0.9;
  margin-top: 180px;
  padding-left: 25px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.stock__info--label {
  color: #ff7435;
}
